* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.webTitleContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  h1 {
    font-style: italic;
    margin-bottom: 0;
    color: #ffdb58;
    text-shadow: 3px 3px 2px rgba(255, 105, 180, 0.7);
    font-size: 6rem;
    font-family: "Space Grotesk";
    font-weight: bolder;
  }
  p {
    margin: 0;
    color: #ffdb58;
    text-shadow: 3px 3px 2px rgba(255, 105, 180, 0.7);
    font-size: 2rem;
    font-family: "Exo 2";
    font-weight: bolder;
  }
}
