.webIntroContainer {
  display: flex;
  flex-direction: column;
  width: 35%;
//   flex-basis: 650px;
  margin-left: 50px;
  margin-right: 50px;
  margin-bottom: 100px;
  background-color: rgba(255, 165, 0, 0.8);
  padding: 25px;
  box-shadow: -5px 5px 4px rgb(253, 94, 83);
  h2 {
    font-family: 'Aoboshi One', serif;
    font-style: italic;
    color: #dc143c;
    /* margin-top: 10px; */
    margin-bottom: 20px;
    text-shadow: 1px 1px 1px rgb(30, 144, 255);
    /* font-size: 32px; */
    font-size: 2rem;
  }
  p {
    font-family: "Roboto Condensed";
    //  font-size: 18px;
    font-size: 1.2rem;
    color: #7d3c98;
    font-weight: 700;
    line-height: 120%;
    margin-top: 20px;
    margin-bottom: 30px;
  }
  a {
    align-self: flex-end;
    font-size: 1.6rem;
    font-style: italic;
    // font-weight: bold;
    text-decoration: underline;
    text-underline-offset: 6px;
    color: #4b0082;
    text-shadow: 2px 2px 1px rgb(231, 76, 60);
    font-family: "Roboto Mono", monospace;
    transition: transform 0.3s ease, text-shadow 0.3s ease;
    
    &:hover {
        transform: scale(1.05);
        text-shadow: 2px 2px 10px rgb(255, 255, 0);
      }

      &:active {
        text-shadow: 2px 2px 2px rgb(255, 105, 180);
        transition: text-shadow 0s;
      }
  }



}
